#playing{
    background: #fff;
    z-index: 10;
    bottom: 0;
    .head{
       position: absolute;
       top: 0;
       left: 0;
       height: 30px;
       width: 100%;
       z-index: 10;
       padding-left: 10px;
       line-height: 30px;
       background: rgba($color: #fff, $alpha: 0.6);
       span{font-size: 20px;color: #000;}
    }
    .video{
        height: 250px;
        width: 100%;
        background: rgba($color: #000000, $alpha: 0.9);
        video{height: 100%;width: 100%;}
    }
    .msgbox{
        width: 100%;
        padding: 10px;
        h3{ line-height: 30px;}
        .bto{
            display: flex;
            padding: 20px 0 10px 0;
            justify-content: space-around;
            p:nth-of-type(1){
                text-align: left;
                span{padding: 0 5px;}
            }
            p:nth-of-type(2){text-align: right;}
        }
    }
    .catalogue{
        min-height: 200px;
        .icon-xiala{
            position: absolute;
            left: 5px;
        }
        a{text-decoration: none;}
        .mui-navigate-right{
            width:96%;
            padding-left: 30px;}
        .mui-navigate-right::after{
           display: none;
        }
        .item{
            position: relative;
            .icon-bofang{
                position: absolute;
                font-size:22px;
                top: 0;
                left: 0;
            }
            p{padding-left: 30px;padding-right: 10px;}
        }
    }
    .others{
        min-height: 300px;
        .top{
            padding: 10px;
            h3{padding-bottom:10px;}
            p{ 
                color:#ff0000;
                font-size: 20px;
                font-weight: bold;
                padding: 10px 0;
            }
            p:nth-of-type(1){color: #000;padding-left: 30px;}
            p:nth-of-type(3){padding-left: 20px;}
            p:nth-of-type(4){text-align: center;}
        }
        img{
            display: block;
            width: 100%;
        }
    }
    .listentBtn{
        position: absolute;
        bottom: 0;
        height: 49px;
        line-height: 49px;
        width: 100%;
        color: #fff;
        font-size:20px;
        background:#23b8ff;
        text-align: center;
    }
}